import {mapGetters, mapState} from 'vuex';

export default {
    data() {
        return {
            id: 0,
            rate: 0,
            detail: {
                list: [],
                log_num: '0',
                user_num: '0',
                order_price: '0',
                order_num: '0',
            },
        }
    },
    components: {
    },
    computed: {
        ...mapGetters('mallConfig', {
            getTheme: 'getTheme',
        }),
        ...mapState({
            stock: state => state.mallConfig.__wxapp_img.stock,
            community: state => state.mallConfig.__wxapp_img.community,
            userInfo: state => state.user.info,
        })
    },
    onLoad(options) { this.$commonLoad.onload(options);
        this.id = options.id;
        this.getList();
    },
    methods: {
        getList() {
            let that = this;
            that.$request({
                url: that.$api.community.activity_log,
                data: {
                    id: that.id
                },
                method: 'post'
            }).then(response=>{
                uni.hideLoading();
                if(response.code == 0) {
                    that.detail = response.data;
                    for(let i in that.detail.list) {
                        that.detail.list[i].index = +i +1;
                        if(that.detail.list[i].index < 10) {
                            that.detail.list[i].index = '0' + that.detail.list[i].index
                        }
                    }
                    if(that.detail.log_num > 0) {
                        that.rate = Math.round(that.detail.user_num / that.detail.log_num * 100)
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                uni.hideLoading();
                that.$hideLoading();
            });
        }
    }
}
